import Vue from 'vue'
import Router from 'vue-router'
// import text from '@/components/text'
import HomePage from '@/components/HomePage/HomePage'
Vue.use(Router)

const routes = [
  {
    path: '/',
    components: {
      default: HomePage
    }
  }
]

export default new Router({
  routes
})



